import { useState } from 'react';

const useSwitchChat = ({ scrollToBottom }) => {
  const [switch–°hat, setSwitchChat] = useState(true);
  const [isSearch, setIsSearch] = useState(false);

  const switchChatToFun = () => {
 		setIsSearch(false); 
		setSwitchChat(true);
    scrollToBottom();
	};

	const switchChatToWork = () => {
 		setIsSearch(false); 
		setSwitchChat(false);
    scrollToBottom();
	};

  return [
    switch–°hat,
    setSwitchChat,
    switchChatToFun,
    switchChatToWork,
    setIsSearch,
    isSearch,
  ];
};

export default useSwitchChat;